import React from 'react';
import { FlexProps } from './FlexProps';
import { Flex as FlexContainer } from './Flex.styled';

const Flex = (props:FlexProps) => {
  const { flexDirection, flex, children } = props;
  return (
    <FlexContainer flex={flex} flexDirection={flexDirection}>
      {children}
    </FlexContainer>
  );
}

export default Flex;