import React from 'react';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import TextField from '@material-ui/core/TextField';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Button from '@material-ui/core/Button';
import MenuItem from '@material-ui/core/MenuItem';
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import Modal from '../commons/Modal/Modal';
import Flex from '../commons/Flex/Flex';
import axios from 'axios';
import moment from 'moment';

const showSuccessToast = (message:any) => {
  toast.success(message);
}

const showErrorToast = (message:any) => {
  toast.error(message);
}

toast.configure();

const AddTutorModal = (props:any) => {
   // add tutor modal state
   const { addTutorOpen, setAddTutorOpen, mode, tutor, setRefetch, access_key } = props;



  // form values
  const [name, setName] = React.useState('');
  const [email, setEmail] = React.useState('');
  const [departament, setDepartament] = React.useState('');
  const [profDegree, setProfDegree] = React.useState('');
  const [dateOfBirth, setDateOfBirth] = React.useState<Date | null>(null);
  const [hireDate, setHireDate] = React.useState<Date | null>(null);
  const [office, setOffice] = React.useState('');
  const [phoneNumber, setPhoneNumber] = React.useState('');
  const [idTutor, setIdTutor] = React.useState('');

  console.log(tutor);

  React.useEffect(() => {
    if(mode==='edit'){
      setName(tutor.name);
      setEmail(tutor.email);
      setPhoneNumber(tutor.phoneNumber);
      setHireDate(tutor.hireDate);
      setDateOfBirth(tutor.dateOfBirth);
      setIdTutor(tutor.idTutor);
      setDepartament(tutor.idDepartament);
      setOffice(tutor.idOffice);
      setProfDegree(tutor.idProfessionalDegree);
    }
  }, []);


  // create the form for axios
  let formData = new FormData();
  formData.append('name', name);
  formData.append('phoneNumber', phoneNumber);
  formData.append('idOffice', office);
  formData.append('hireDate', moment(hireDate).format('YYYY-MM-DD'));
  formData.append('dateOfBirth', moment(dateOfBirth).format('YYYY-MM-DD'));
  formData.append('idDepartament', departament);
  formData.append('idProfessionalDegree', profDegree);
  formData.append('email', email);
  formData.append('access_token', access_key);
  if(idTutor){
    formData.append('idTutor', idTutor);
  }

  const submitForm = async () => {
    await axios({
      method: 'post',
      url: 'http://localhost:8080/add-tutor',
      data: formData
    })
    .then((response) => {
      showSuccessToast(response.data);
      setAddTutorOpen(false);
      setRefetch(Math.random())
    }, (error) => {
      showErrorToast(error);
      setAddTutorOpen(false);
    });
  } 

  const editTutor = async () => {
    await axios({
      method: 'post',
      url: 'http://localhost:8080/update-tutor',
      data: formData
    })
    .then((response) => {
      showSuccessToast(response.data);
      setAddTutorOpen(false);
      setRefetch(Math.random())
    }, (error) => {
      showErrorToast(error);
      setAddTutorOpen(false);
    });
  }

  const handleClose = () => {
    setAddTutorOpen(false);
  };

  const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    formControl: {
      minWidth: 120,
      width:'100%',
    },
    selectEmpty: {
      marginTop: theme.spacing(2),
    },
  }),
);

const classes = useStyles();

  return (
    <React.Fragment>
      <Modal handleClose={() => handleClose} title={mode === 'new' ? 'Add tutor' : `Edit tutor ${tutor.name}`} open={addTutorOpen}>
        <div>
        <Flex>
          <TextField id="outlined-basic" onChange={(e) => setName(e.target.value)} value={name} label="Name" style={{width:'100%'}}/>
        </Flex>
        
        <Flex>
          <TextField id="outlined-basic" value={phoneNumber} onChange={(e) => setPhoneNumber(e.target.value)} style={{width:'100%'}} label="Phone number" />
        </Flex>

        <Flex>
          <TextField id="outlined-basic" value={email} onChange={(e) => setEmail(e.target.value)} style={{width:'100%'}} type="email" label="email" />
        </Flex>
        <Flex flexDirection="row">
        <Flex flex={1}>

        <FormControl className={classes.formControl}>
           <InputLabel id="demo-simple-select-label">Office</InputLabel>
            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={office}
              onChange={(e:any) => setOffice(e.target.value)}
              style={{width:'100%'}}
            >
              <MenuItem value={1}>B226</MenuItem>
              <MenuItem value={2}>A210</MenuItem>
              <MenuItem value={3}>Electro2</MenuItem>
              <MenuItem value={4}>A210</MenuItem>
              <MenuItem value={5}>B228</MenuItem>
              <MenuItem value={6}>B225</MenuItem>
            </Select>
          </FormControl>
        </Flex>

        <Flex flex={1}>
          <FormControl className={classes.formControl}>
           <InputLabel id="demo-simple-select-label">Prof. Degree</InputLabel>
            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={profDegree}
              onChange={(e:any) => setProfDegree(e.target.value)}
              style={{width:'100%'}}
            >
              <MenuItem value={1}>Drd.</MenuItem>
              <MenuItem value={2}>Asoc.</MenuItem>
              <MenuItem value={3}>As.</MenuItem>
              <MenuItem value={4}>Sl.</MenuItem>
              <MenuItem value={5}>Conf.</MenuItem>
              <MenuItem value={6}>Prof.</MenuItem>
            </Select>
          </FormControl>
        </Flex>
        </Flex>
        <Flex flex={1}>
        <FormControl className={classes.formControl}>
           <InputLabel id="demo-simple-select-label">Departament</InputLabel>
            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={departament}
              onChange={(e:any) => setDepartament(e.target.value)}
            >
              <MenuItem value={1}>Communications</MenuItem>
              <MenuItem value={2}>Applied Electronics</MenuItem>
              <MenuItem value={3}>Measurements and Optic Electronics</MenuItem>
              <MenuItem value={4}>Automation and Applied Informatics</MenuItem>
              <MenuItem value={5}>Computers and Information Technology</MenuItem>
              <MenuItem value={6}>Mathematics</MenuItem>
              <MenuItem value={7}>The physical basis of engineering</MenuItem>
              <MenuItem value={8}>Management</MenuItem>
              <MenuItem value={9}>Economic and socio-human sciences</MenuItem>
            </Select>
          </FormControl>
        </Flex>
        <Flex flexDirection="row">
        <MuiPickersUtilsProvider utils={DateFnsUtils}>
        <Flex>
        <KeyboardDatePicker
          margin="normal"
          id="date-picker-dialog"
          label="Hire date"
          format="MM/dd/yyyy"
          value={hireDate}
          onChange={setHireDate}
          KeyboardButtonProps={{
            'aria-label': 'change date',
          }}
        />
        </Flex>
        <Flex>
        <KeyboardDatePicker
          margin="normal"
          id="date-picker-dialog"
          label="Date of Birth"
          format="MM/dd/yyyy"
          value={dateOfBirth}
          onChange={setDateOfBirth}
          KeyboardButtonProps={{
            'aria-label': 'change date',
          }}
        />
        </Flex>
         </MuiPickersUtilsProvider>
         </Flex>
        {mode === 'new' && (
          <Button variant="contained" color="primary" onClick={submitForm}>
          Add tutor
        </Button>
        )}
        {mode === 'edit' && (
        <Button variant="contained" color="primary" onClick={editTutor}>
          Save tutor
        </Button>
        )}
        
        </div>
      </Modal>
    </React.Fragment>
  )
}

export default AddTutorModal;
